import React, { Component } from 'react'

class PassInfo extends Component {

    state = {
        adults: 1,
        childs: 0,
        infants: 0,
        travelClass: "Economy"
    }

    handleFlightClassClick = (e)=>{
        this.setState({
            travelClass: e.target.id
        })
    }

    getCheckedVal = (id)=>{
        if(this.state.travelClass === id) return true;
        return false;
    }

    handleFlightClassChange = (e)=>{
        
    }

    handlePassengersAddition = (e)=>{
        let id = e.target.id;
        let maxVal = 10;
        if(id === "infants") maxVal = 3
        if(id === "childs") maxVal = 8
        this.setState({
            [id]: this.state[id] + 1 < maxVal ? this.state[id] + 1 : this.state[id]
        })
    }

    handlePassengersSubtraction = (e)=>{
        let id = e.target.id;
        this.setState({
            [id]: this.state[id] - 1 < 1 ? this.state[id] : this.state[id] - 1
        })
    }

    submitTheFlightInfo = ()=>{
        this.props.passInfo(this.state);
    }

    render(){
        return (
            <div id="pass-info" className="dropdown-content z-depth-2">
                <div id="pass-wrapper">
                    <div id="passengers">
                        <h6>Passengers</h6>
                        <div id="passengers-list">
                            <h6>Adult (+12 yrs)</h6>
                            <div className="val">
                                <button className="btn blue" id="adults" onClick={this.handlePassengersSubtraction}>-</button>
                                <span>{ this.state.adults }</span>
                                <button className="btn blue" id="adults" onClick={this.handlePassengersAddition}>+</button>
                            </div>
                        </div>

                        <div id="passengers-list">
                            <h6>Child (2-12 yrs)</h6>
                            <div className="val">
                                <button className="btn blue" id="childs" onClick={this.handlePassengersSubtraction}>-</button>
                                <span>{ this.state.childs }</span>
                                <button className="btn blue" id="childs" onClick={this.handlePassengersAddition}>+</button>
                            </div>
                        </div>

                        <div id="passengers-list">
                            <h6>Infant (0-2 yrs)</h6>
                            <div className="val">
                                <button className="btn blue" id="infants" onClick={this.handlePassengersSubtraction}>-</button>
                                <span>{ this.state.infants }</span>
                                <button className="btn blue" id="infants" onClick={this.handlePassengersAddition}>+</button>
                            </div>
                        </div>
                    </div>

                    <div id="flight-class-info">
                        <h6>Travel class</h6>
                        <div>
                            <label>
                                <input id="Economy" className="with-gap valign-center" checked={this.getCheckedVal("Economy")} onClick={this.handleFlightClassClick} onChange={this.handleFlightClassChange} type="radio" />
                                <span>Economy</span>
                            </label>
                            <label>
                                <input id="Premium Economy" className="with-gap valign-center" checked={this.getCheckedVal("Premium Economy")} onClick={this.handleFlightClassClick} onChange={this.handleFlightClassChange} type="radio"  />
                                <span>Premium Economy</span>
                            </label>

                            <label>
                                <input id="Business" className="with-gap valign-center" checked={this.getCheckedVal("Business")} onClick={this.handleFlightClassClick} onChange={this.handleFlightClassChange} type="radio" />
                                <span>Business</span>
                            </label>

                            <label>
                                <input id="First Class" className="with-gap valign-center" checked={this.getCheckedVal("First Class")} onClick={this.handleFlightClassClick} onChange={this.handleFlightClassChange} type="radio" />
                                <span>First Class</span>
                            </label>
                        </div>
                        <button className="btn blue waves-effect" id="apply" onClick={this.submitTheFlightInfo} >Apply</button>
                    </div>
                </div>
            </div>
        )
    }
}

export default PassInfo
