import React, { Component } from 'react'
import { Carousel, Autocomplete, Datepicker, Dropdown, Modal } from 'materialize-css/dist/js/materialize.min.js'
import PassInfo from './PassInfo'
import VisitorModel from './VisitorModel'
import axios from 'axios'

let prefixUrl = "/";

let autocompleteData = {
    "Goa": null,
    "Varanasi": null,
    "Mumbai ( Shivaji Maharaj Airport )": null,
    "Delhi ( Delhi International Airport )": null
}

class FlightCaro extends Component {

    state = {
        isRoundTrip: true,
        fromCity: '',
        toCity: '',
        departDate: null,
        returnDate: null,
        flightAndPassInfo: {
            adults: 1,
            childs: 0,
            infants: 0,
            travelClass: "Economy"
        }
    }

    componentDidMount(){
        if(window.screen.availWidth > 750){
            const flightCaro = document.querySelector('.booking-caro');
            let instance = Carousel.init(flightCaro,{
                fullWidth: true,
                duration: 400
            })

            setInterval(()=>{
                instance.next()
            },6000)
        }

        var fromCity = document.querySelector('.fromCity');
        Autocomplete.init(fromCity,{
            data: autocompleteData,
            limit: 4,
            onAutocomplete: (val)=>{
                this.setState({
                    fromCity: val
                })
            }
        })

        var toCity = document.querySelector('.toCity');
        Autocomplete.init(toCity,{
            data: autocompleteData,
            limit: 4,
            onAutocomplete: (val)=>{
                this.setState({
                    toCity: val
                })
            }
        })

        var departDate = document.querySelector('.departDate');
        Datepicker.init(departDate,{
            defaultDate: new Date(),
            minDate: new Date(),
            onSelect: (selectedDate)=>{
                this.setState({
                    departDate: selectedDate
                });
            }
        })

        var returnDate = document.querySelector('.returnDate');
        Datepicker.init(returnDate,{
            defaultDate: new Date(),
            minDate: new Date(),
            onSelect: (selectedDate)=>{
                this.setState({
                    returnDate: selectedDate
                })
                console.log(this.state);
            }
        })

        var flightInfo = document.getElementById('flight-info');
        this.flightDropdown =  Dropdown.init(flightInfo,{
            coverTrigger: false,
            constrainWidth: false,
            closeOnClick: false
        })

        var userInfo = document.getElementById('user-info');
        this.userDetailsModel =  Modal.init(userInfo,{
            
        })

    }

    getFlightAndPassInfo = (info)=>{
        this.setState({
            flightAndPassInfo: info
        })
        this.flightDropdown.close();
    }

    handleChange = ()=>{
        this.setState({
            isRoundTrip: !this.state.isRoundTrip
        })
    }

    handleClick = ()=>{

    }

    submitFlightData = ()=>{
        if(this.state.fromCity === '' || this.state.toCity === '' || this.state.departDate == null){
            alert('Please Fill In All The Details')
            return;
        }

        if(this.state.isRoundTrip && this.state.returnDate == null){
            alert("Please Enter Return Date");
            return;
        }

        this.userDetailsModel.open();
    }


    sendAllDataToServer = (userData)=>{

        this.userDetailsModel.close();
        
        axios.post(`${prefixUrl}services/`,{
            userData: userData,
            data: this.state,
            serviceType: 'Flight'
        }).then((res)=>{
            alert("Thanks For Choosing Us we will contact you")
        }).catch((err)=>{
            console.log(err);
        })
        
    }

    render(){
        let travellersInfo = this.state.flightAndPassInfo
        let notravellers = travellersInfo.adults + travellersInfo.childs + travellersInfo.infants; 
        return (
            <div id="booking-activity">
                <div className="carousel carousel-silder default-caro booking-caro">
                    <a className="carousel-item" href="#one!">
                        <div className="caro-item" id="caro-1"></div>
                    </a>
                    <a className="carousel-item" href="#two!">
                        <div className="caro-item" id="caro-2"></div>
                    </a>
                    <a className="carousel-item" href="#three!">
                        <div className="caro-item" id="caro-3"></div>
                    </a>  
                </div>     

                <div id="flight-model" className="container">
                    <div id="flight-model-content">
                        <div id="flight-iconBox">
                            <i className="material-icons">flight_takeoff</i>
                            <span>FLIGHTS</span>
                        </div>

                        <div id="options">
                            <label>
                                <input className="with-gap" onChange={this.handleClick} onClick={this.handleChange} name="one-way" type="radio" checked={this.state.isRoundTrip} />
                                <span>Round Trip</span>
                            </label>

                            <label>
                                <input className="with-gap" onChange={this.handleClick} onClick={this.handleChange} name="one-way" type="radio" checked={!this.state.isRoundTrip} />
                                <span>One Way</span>
                            </label>
                        </div>

                        <div className="row">
                            <div className="col s12 m9">
                                <div className="row">
                                    <div className="input-field col s12 m4">
                                        <i className="material-icons prefix">flight_takeoff</i>
                                        <input type="text" id="form-city-input" className="autocomplete fromCity" placeholder="From City"></input>
                                    </div>

                                    <div className="input-field col s12 m4">
                                        <i className="material-icons prefix">flight_land</i>
                                        <input type="text" id="to-city-input" className="autocomplete toCity" placeholder="To City"></input>
                                    </div>

                                    <div className="input-field col s12 m4">
                                        <i className="material-icons prefix">date_range</i>
                                        <input type="text" className="datepicker departDate" placeholder="Depart Date"></input>
                                    </div>
                                </div>

                                <div className="row">
                                    <div className="input-field col s12 m4">
                                        <i className="material-icons prefix">date_range</i>
                                        <input type="text" className="datepicker returnDate" placeholder="Return Date" disabled={!this.state.isRoundTrip}></input>
                                    </div>

                                    <div id="flight-info" className="col s12 m4 dropdown-trigger" data-target='pass-info'>
                                        <span>{ notravellers } Travellers, { travellersInfo.travelClass }</span>
                                        <i className="material-icons">person</i>
                                    </div>

                                    <PassInfo passInfo={this.getFlightAndPassInfo} />

                                </div>

                            </div>
                            <div className="col s12 m2 offset-m1">
                                <button onClick={this.submitFlightData} className="waves-effect waves-light btn blue" id="search">Search</button>
                            </div>
                        </div>

                    </div>
                </div>
                <VisitorModel sendData={this.sendAllDataToServer} />
            </div>
        )
    }
}

export default FlightCaro
